import React, {useEffect, useState} from 'react';
import {Link} from "react-router-dom";
import {Modal, ModalBody, ModalFooter} from "reactstrap";
import axios from "axios";
import {API_PATH} from "./const";

const News = (props) => {
    const [books, setBooks] = useState([])
    const books2 = [
        {
            id: 1,
            name: "Popda kamondan otish boyicha musobaqa otkazildi",
            date: "12-12-2022",
            file: "/images/n1.jpg",
            content: "Namangan viloyati Pop tumanida joylashgan 1-sonli BOSMda Ozbekiston Respublikasi Milliy gvardiyasi Namangan viloyati Qoriqlash boshqarmasi hamda viloyat Turizm va sport bosh boshqarmasi bilan hamkorlikda kamondan otish sport turi boyicha musobaqa otkazildi. Unda ishtirokchilar ustozlari tomonid...\n" +
                "\n"
        },
        {
            id: 2,
            name: "Davlatimiz gerbi  faxrimiz, gururimiz ramzi",
            date: "31-10-2022",
            file: "/images/n2.jpg",
            content: "Ozbekiston Respublikasi Davlat Gerbi  mamlakatimiz davlat mustaqilligining ramzlaridan biridir. Gururimiz va iftixorimiz timsoli bolgan davlat Gerbining 30 yilligi nishonlanmoqda. Bugun Urganch tumani Axborot kutubxona markazi hodimi tumandagi Barkamol avlod bolalar ijodiyot maktabida bolib Yosh musavvirlar togarak azolari bilan Gerbimiz  faxrimiz deb nomlangan davra suxbati tashkil etildi. Markaz hodimi Davlatimiz Gerbi togrisida yosh musavvirlarga malumot berdi. Togarak azolariga davlat Geribi rasmini tasvirlash toshirigi berildi. Davlat ramzlari tasvirlangan Gerbimiz suratini chiroyli chizgan yoshlarga kutubxonamiz tomonidan kitoblar sovga qilindi."
        },
        {
            id: 3,
            name: "Yol yolakay kitob oqiymiz",
            date: "03-11-2022",
            file: "/images/n3.jpg",
            content: "Urganch tumani axborot-kutubxona markazi hodimlari tomonidan Qorovul qishlogining markaziy kochalarida Yol yolakay kitob oqiymiz loyihasi asosida kitob taqdimotlari otkazildi. Yolovchilarga markazda yaratilgan imkoniyatlar, kitob fondi va yangi kelgan adabiyotlar haqida malumotlar berildi."
        }
    ]


    const getBooks = () => {
        axios.get(API_PATH + `items/news`)
            .then(res => {
                setBooks(res.data.data.results)
            })
    }
    useEffect(() => {
        getBooks()


    }, [])

    return (
        <div className="documents">
            <div className="container">
                <div className="container d-flex justify-content-between">
                    <h2 className="mt-5">Yangiliklar</h2>
                </div>
                <div className="row">
                    <div className="col-md-12">
                        {
                            books?.map(item => (
                                <div className="col-md-12 ">
                                    <div className="row">
                                        <div className="col-md-4">
                                            <img src={item.file}/>
                                        </div>
                                        <div className="col-md-8">
                                            <h3>{item.name?.length > 100 ? item.name?.slice(0, 100) + "..." : item.name}</h3>
                                            <span>{item.date}</span>
                                            <p className="text-white">{item.content?.length > 200 ? item?.content?.slice(0, 200) + "..." : item.content}</p>
                                            <Link to={`/detail-news-global/${item.id}`}>Batafsil...</Link>

                                        </div>
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                </div>
            </div>

        </div>
    );
};

export default News;